.trail-map, .home-page,.home-right{
  vertical-align: top;
}

.trail-map {
  width: 75vw;
  height: 90vh;
  overflow: auto;
  position: relative;
  display: inline-block;
}

#trailMap {
  width: 100%;
  height: 100%;
}

.home-left,.home-right{
  display: inline-block;
  width: 10vw;
  height: 90vh;
}

.overlay {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100%;
  border: 1px solid red;
}

#bearPeak {
  position: relative;
  left: 141px;
  top: 76px;
  width: 188px;
  height: 69px;
  /* border: 5px purple solid; */
}

.status {
  position: relative;
  top: 49px;
  left: -32px;
  height: 25px;
  width: 25px;
  background-color: rgb(18, 212, 18);
  border-radius: 50%;
  display: inline-block;
}

.context-menu{
  position: absolute;
}